.card-list-swimlane {
    display: flex;
    justify-content: space-around;
    width: 400px;
    margin: 10px auto;
    padding: 30px 0;
    border: 1px solid black;
    flex-wrap: wrap;
}

@media only screen and (min-width: 640px) {
    .card-list-swimlane {
        width: 80%;
    }
}